ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin: 0;
}

input {
  outline: none;
}

textarea {
  outline: none;
  resize: none;
}

img {
  display: block;
  max-width: 100%;
}
